@import url(https://fonts.googleapis.com/css?family=Playfair+Display&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, button {
  font-family: "Playfair Display", "Segoe UI", "Roboto", "Fira Sans", serif;
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

#quote-box {
  border-radius: 5px;
  background-color: white;
  width: 500px;
  padding: 50px;
}

#text {
  text-align: center;
  font-size: 25px;
}

#author {
  text-align: right;
  margin-top: 10px;
}

.buttons {
  margin-top: 28px;
}

.buttons * {
  height: 40px;
  padding: 10px 15px;
  border: none;
  border-radius: 2px;
  text-align: center;
  color: white;
  cursor: pointer;
}

#tweet-quote {
  float: left;
  text-decoration: none;
}

#new-quote {
  float: right;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

